let BobaValue = '0.00'
let liveValue = '0.00'

fetch(
  'https://api.vlad.finance/price.php?key=6547643&pool=0xace10b48c666668aac82526b6181e57ffe0ccc97&token=0x5481a517028813956005d77bcd0f561c7276b1e3&decimals=9',
)
  .then((res) => res.json())
  .then(
    (result) => {
      if (result.status === true) {
        BobaValue = result.data
      } else {
        BobaValue = '0.00'
      }
    },
    (error) => {
      BobaValue = '0.00'
    },
  )
  .catch(() => {
    BobaValue = '0.00'
  })

fetch(
  'https://api.vlad.finance/price.php?key=6547643&pool=0xace10b48c666668aac82526b6181e57ffe0ccc97&token=0x5481a517028813956005d77bcd0f561c7276b1e3&decimals=9',
)
  .then((res) => res.json())
  .then(
    (result) => {
      if (result.status === true) {
        liveValue = result.data
      } else {
        liveValue = '0.00'
      }
    },
    (error) => {
      liveValue = '0.00'
    },
  )
  .catch(() => {
    liveValue = '0.00'
  })

